#view-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100vw;
    height: 100vh;
    overflow: hidden;

    /*z-index: 1;*/

    margin: 0;
    padding: 0;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

#sections-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    
    /*
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    */

    display: block;
    margin: 0;
    padding: 0;
}

@-webkit-keyframes top-enter {
    from {
        margin-top: 500%;
    }

    to {
        margin-top: 0px;
    }
}

@keyframes top-enter {
    from {
        margin-top: 500%;
    }

    to {
        margin-top: 0px;
    }
}

@-webkit-keyframes bottom-enter {
    from {
        margin-bottom: 500%;
    }

    to {
        margin-bottom: 0px;
    }
}

@keyframes bottom-enter {
    from {
        margin-bottom: 500%;
    }

    to {
        margin-bottom: 0px;
    }
}

.section-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    height: 100%;
    position: relative;

    margin: 0;
    padding: 0;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    
    background: none;
}

.page-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;


    display: -ms-flexbox;


    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    color: white;

    background: var(--section-container-bg);
}

.page-container:nth-child(2n) {
    background: var(--section-container-alt-bg);    
}

.page-container.bottom {
    -webkit-animation-name: bottom-enter;
            animation-name: bottom-enter;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
}

.page-container.top {
    -webkit-animation-name: top-enter;
            animation-name: top-enter;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
}



.background-layer {
    display: inline-block;
    position: absolute;
    -o-object-fit: fill;
       object-fit: fill;
    -o-object-position: center;
       object-position: center;
    height: 100%;
}

@media only screen and (min-width: 960px) {
    #view-container {    
        display: -ms-flexbox;    
        display: flex;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
    }
}





