.auto-text-container {
    --line-height: 1.5em;
    --cursor-size: 1.3em;

    -webkit-box-sizing: border-box;

            box-sizing: border-box;
    max-width: 100%;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;

    display: inline-block;
    z-index: 1;

    margin: 0;
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;

    font-weight: bold;
    font-family: var(--font-family);
    line-height: var(--line-height);

    overflow-wrap: break-word;

    background: rgba(0, 0, 0, 0.75);
    color: var(--accent-blue);
}

.auto-text-container.large {
    font-size: var(--large-font);
}

.auto-text-container.medium {
    font-size: var(--medium-font);
}

.auto-text {
    display: inline-block;
}

.auto-text.spring {
    -webkit-transition: -webkit-transform 0.35s;
    transition: -webkit-transform 0.35s;
    -o-transition: transform 0.35s;
    transition: transform 0.35s;
    transition: transform 0.35s, -webkit-transform 0.35s;
}

.auto-text.spring:hover {
    -webkit-transform: scale(1.4, 1.4);
        -ms-transform: scale(1.4, 1.4);
            transform: scale(1.4, 1.4);
}


@-webkit-keyframes stand-up {
    from {
        -webkit-transform: rotateX(90deg);
                transform: rotateX(90deg);
    }

    to {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }
}


@keyframes stand-up {
    from {
        -webkit-transform: rotateX(90deg);
                transform: rotateX(90deg);
    }

    to {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }
}
.auto-text.stand-up {
    display: inline-block;
    color: var(--accent-blue);

    

    -webkit-animation-name: stand-up;

    

            animation-name: stand-up;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}



@-webkit-keyframes cursor-blink {
    to {
        opacity: 1;
    }
}



@keyframes cursor-blink {
    to {
        opacity: 1;
    }
}

.auto-text-cursor {
    opacity: 0;
    font-size: var(--cursor-size);

    -webkit-animation-name: cursor-blink;

            animation-name: cursor-blink;
    -webkit-animation-duration: 0.6s;
            animation-duration: 0.6s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
}


